@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto&display=swap);
* {
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;

}

.openSans {
    font-family: 'Open Sans', sans-serif;
}

.roboto {
    font-family: 'Roboto', sans-serif;
}

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
}

::-webkit-scrollbar {
    width: 8px;
    background-color: transparent;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
    background-color: #ED6A5A;
}
.mainContentLanding {
    background-color: #1c1c1c;
}

.container {
    padding-left: 0;
    padding-right: 0;
}
.sectionWrapper {
    position: relative;
}

.titleSection {
    font-size: 3rem;
}

.headline {
    color: white;
}

.watermark {
    color: rgba(255, 255, 255, 0.05);
    font-size: 20rem;
    font-weight: bold;
}

/* Medium screen */
@media only screen and (min-width: 1200px) and (max-width: 1400px) {
    .watermark {
        font-size: 16rem;
    }
}

/* Medium screen */
@media only screen and (width: 1440px) {
    .watermark {
        font-size: 18rem;
    }
}
#home {
    margin-bottom: 2rem;
}

.contentHome {
    z-index: 10;
    position: relative;
}

.orangeBox {
    position: absolute;
    right: -10px;
    top: -8vh;
    z-index: 0;
    width: 24vw;
    height: 90vh;
    object-fit: cover;
}

.blueLine {
    margin-top: 1.5rem;
    width: 100%;
}

.socialIcon {
    margin-right: 1.5rem;
    z-index: 5;
}

.description {
    margin-top: 3rem;
    color: white;
    line-height: 3;
}

.highlight {
    font-weight: bold;
    color: #0094C6;
}

.profilePictureContainer {
    height: 100%;
    padding-left: 4rem;
}

.profilePicture {
    height: 100%;
}

.watermarkHome {
    top: 20%;
    position: absolute;
    z-index: 0;
}

.socialList {
    z-index: 10;
}

/* Handphone */
@media only screen and (max-width: 500px) {}

/* Medium screen */
@media only screen and (min-width: 1200px) and (max-width: 1400px) {
    .orangeBox {
        width: 30vw;
        height: 85vh;
    }
}

/* Large screen */
@media only screen and (width: 1440px) {
    .orangeBox {
        width: 25vw;
        height: 78vh;
    }
}
.about {
    margin-bottom: 10rem;
}

.blueBox {
    width: 70%;
    position: absolute;
}

.contentAbout {
    padding-top: 105px;
}

.watermarkAbout {
    top: 0;
    right: 4%;
    width: 50%;
    position: absolute;
    z-index: 0;
    text-align: right;
    line-height: 0.9;
}

.loading {
    height: calc(502px + 5rem);
    width: 100%;
}

/* Medium screen */
@media only screen and (min-width: 1200px) and (max-width: 1400px) {
    .contentAbout {
        padding-top: 92px;
    }
}

/* Large screen */
@media only screen and (width: 1440px) {
    .contentAbout {
        padding-top: 105px;
    }
}
.backgroundContainer {
    overflow-y: auto;
    height: 500px;
}

.backgroundList {
    padding-left: 3rem;
    margin-left: 1rem;
    list-style: none;
    border-left: 2px solid white;
}

.backgroundItem {
    position: relative;
    margin-bottom: 2rem;
    border-bottom: 1px solid white;
    padding-bottom: 2rem;
}

.backgroundItem::after {
    content: '';
    position: absolute;
    top: -0.7rem;
    left: -3.6rem;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    content: '\25CF';
    font-size: 30px;
    color: #1c1c1c;
}

.buttonBackground {
    padding: 0.75rem 1rem;
    margin: 0.5rem;
    background-color: transparent;
    border: none;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}

.buttonBackground:active {
    border: none;
}

.buttonBackground.active {
    background-color: #ED6A5A;
    color: white;
}

.buttonBackground.active:disabled {
    color: grey;
}

.buttonBackground.active::before {
    content: "";
    height: 70%;
    width: 90%;
    border-left: 2px solid #ED6A5A;
    border-top: 2px solid #ED6A5A;
    position: absolute;
    top: 0;
    left: 0;
}
.orangeBoxPortfolio {
    position: absolute;
    top: 15vh;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 0;
}

.portfolio {
    margin-bottom: 35vh;
}

.workContainer {
    z-index: 10;
}

.watermarkPortfolio {
    position: absolute;
    top: 80vh;
    z-index: 1;
}

.contentPortfolio {
    padding-top: 8rem;
}

.loadingPortfolio {
    height: 60vh;
    width: 100%;
}
.cardContainer {
    margin-bottom: 2rem;
    background-color: transparent;
    border-radius: 0;
    border: 2px solid #FFF8F0;
    color: white;
}

.cardImage {
    background-color: #FFF8F0;
    padding: 1rem 0.5rem;
    width: 100%;
}

.cardTitle {
    min-height: 2.3em;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cardDescription {
    text-align: justify;
    min-height: 8em;
}

.buttonPortfolio {
    color: #0094C6;
    width: 100%;
    padding: 0.5rem 1rem;
    background-color: #FFF8F0;
    border: none;
    -webkit-transition: all 0.15s linear;
    transition: all 0.15s linear;
}

.buttonPortfolio:not(:disabled):hover {
    background-color: rgb(184, 179, 173);
}

.buttonPortfolio:focus {
    box-shadow: none;
    outline: none;
}

.buttonPortfolio.active {
    background-color: #0094C6;
    color: white;
}

.buttonPortfolio.active:hover {
    background-color: rgb(0, 115, 153);
    color: white;
}

.buttonPortfolio:disabled {
    color: grey;
    background-color: lightgrey;
}
.divider {
    overflow: hidden;
    text-align: center;
    font-size: 1.5rem;
    color: #ED6A5A;
}

.divider:before,
.divider:after {
    background-color: #ED6A5A;
    content: "";
    display: inline-block;
    height: 3px;
    position: relative;
    vertical-align: middle;
    width: 50%;
}

.divider:before {
    right: 1em;
    margin-left: -50%;
}

.divider:after {
    left: 1em;
    margin-right: -50%;
}

.form {
    background-color: transparent;
    border-radius: 0;
    color: white;
    border: 1px solid white;

}

.form:focus {
    background-color: transparent;
    border-radius: 0;
    color: white;
    border: 1px solid white;
    box-shadow: none;
}

.formLabel {
    position: absolute;
    pointer-events: none;
    left: 1rem;
    top: 0.5rem;
    -webkit-transition: 0.2s ease all;
    transition: 0.2s ease all;
    color: white;
    font-size: 1.25rem;
}

.form:focus~.formLabel,
.form:not(:focus):valid~.formLabel {
    top: -0.6rem;
    left: 1rem;
    font-size: 0.8rem;
    opacity: 1;
    background-color: #1c1c1c;
    padding-left: 0.4rem;
    padding-right: 0.4rem;
}

textarea {
    resize: none;
}

.custom-control-label {
    color: white;
}

.custom-control-input:checked~.custom-control-label::before {
    border-color: #ED6A5A;
    background-color: #ED6A5A;
}

.custom-control-input:not(:disabled):active~.custom-control-label::before {
    border-color: rgb(228, 163, 155);
    background-color: rgb(228, 163, 155);
}

.custom-control-input:focus:not(:checked)~.custom-control-label::before {
    border-color: rgb(228, 163, 155);
    background-color: rgb(228, 163, 155);
}

.custom-control-input:focus~.custom-control-label::before {
    box-shadow: none;
}

.form-control:disabled {
    background-color: rgb(68, 68, 68);
}

.formLabel.disabled {
    color: grey;
}

.buttonContact {
    color: white;
    width: 100%;
    padding: 0.5rem 1rem;
    background-color: #ED6A5A;
    border: none;
    -webkit-transition: all 0.15s linear;
    transition: all 0.15s linear;
}

.buttonContact:hover {
    background-color: rgb(212, 124, 112);
}

.buttonContact:focus {
    box-shadow: none;
    outline: none;
}

.messagesContainer {
    overflow-y: auto;
    max-height: 70vh;
}

/* Medium screen */
@media only screen and (min-width: 1200px) and (max-width: 1400px) {
    .messagesContainer {
        max-height: 65vh;
    }
}

/* Large screen */
@media only screen and (width: 1440px) {
    .messagesContainer {
        max-height: 60vh;
    }
}

.whiteCircle {
    width: 256px;
    position: absolute;
    bottom: 0;
    right: 25%;
}

.backButton {
    cursor: pointer;
    color: #0094C6;
}
.contactImageContainer {
    width: 2rem;
    text-align: center;
    margin-right: 1rem;
    margin-left: 1rem;
}

.contactImage {
    width: 1.5rem;
    object-fit: scale-down;
}

.contactText {
    color: #FFF8F0;
    margin-bottom: 0;
}

.messageContainer {
    padding: 1rem;
    margin-right: 1rem;
    background-color: #0094C6;
    box-shadow: 0.5rem 0.5rem 0px #ED6A5A;
}

.messageContent {
    padding-left: 1.5rem;
    border-left: 3px solid #ED6A5A;
}
.navbar {
    min-height: 8vh;
}

.navbar-light .navbar-nav .nav-link {
    color: #000000;
    font-weight: bold;
    z-index: 10;
    -webkit-transition: all ease-in 0.3s;
    transition: all ease-in 0.3s;
}

.navbar-light .navbar-nav .nav-link:focus,
.navbar-light .navbar-nav .nav-link:hover {
    color: black;
}

.sidebar {
    position: fixed;
    top: calc(50% - 1.5rem*4);
    right: 2vw;
    z-index: 1000;
}

.sidebarDot {
    text-decoration: none !important;
    font-size: 1.5rem;
    color: white;
    cursor: pointer;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.sidebarDot:hover {
    font-size: 1.5rem;
    color: #0094C6;
}

.sidebarDot.active {
    font-size: 2.5rem;
    color: #0094C6;
}
.mainContentComing {
    height: 100vh;
}

.comingSoonAsset {
    position: absolute;
    bottom: 0;
    width: 65%;
}

.comingSoonText {
    width: 25%;
    color: #0094C6;
}

.mobileComingSoon {
    padding: 1rem;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Medium screen */
@media only screen and (min-width: 1200px) and (max-width: 1400px) {
    .comingSoonAsset {
        width: 75%;
    }
}

/* Large screen */
@media only screen and (width: 1440px) {
    .comingSoonAsset {
        width: 70%;
    }
}
.wrapperNotFound {
    background-color: #ffffff;
    height: 92vh;
}

.mainContentNotFound {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.imageNotFound {
    height: 40vh;
}
